import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function targetLow(tableName: string) {
  const title = "Target Low"
  return genericColumn({
    endpoint: "companyPriceTarget",
    columnKeys: ["targetLow"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Shows the downside risk or pessimistic broker/analysts expectations.",
    extraFinanceData: entityIds => ({ companyCurrency: { entityIds } }),
    cell: info => {
      const currency = info.row.original.financialData.companyCurrency?.data?.tradingCurrency
      return (
        <WatchlistDataTablePriceCell
          queryResult={info.row.original.financialData.companyPriceTarget}
          tooltip={"Explain the Target Low"}
          currency={currency}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `Explain how recent market trends could contribute to ${info.row.original.entityInfo.name} bear-case scenario, potentially resulting in a target low.`,
              value: info.row.original.financialData.companyPriceTarget?.data?.targetLow,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyPriceTarget?.data?.targetLow}
        />
      )
    },
  })
}

export { targetLow }
